<template>
  <div class="manage-examine manage-dev">
    <div class="title-line">
      <p class="title">项目测验</p>
      <p class="org">当前挂靠单位：{{ BELONGTO_LIST[this.user_info.org] }}</p>
    </div>
    <el-tabs class="table-tab" v-model="status" @tab-click="handleClick">
      <el-tab-pane label="全部项目" name="all"></el-tab-pane>
      <el-tab-pane label="已出行队伍" name="4"></el-tab-pane>
      <el-tab-pane label="已返回未测验队伍" name="5"></el-tab-pane>
      <el-tab-pane label="已通过测验项目" name="7"></el-tab-pane>
      <el-tab-pane label="未通过测验项目" name="6"></el-tab-pane>
      <el-tab-pane label="校级答辩项目" name="8"></el-tab-pane>
    </el-tabs>
    <!-- <p class="manage-examine-remain title">剩余项目数：{{ pagination.total }}</p> -->
    <pager
      :pageSize="6"
      :requestFunc="getList"
      :errorMsg="'无法获取'"
      :extraData="status"
      ref="refresh">
      <el-table
        v-loading="loading"
        class="table-project"
        slot-scope="props" 
        :data="props.list">
        <el-table-column label="项目名称" width="160">
          <template slot-scope="scope">
            <div class="line">{{scope.row.project_name}}</div>
          </template>
        </el-table-column>
        <el-table-column label="实践地点" width="220">
          <template slot-scope="scope">
            <div class="line">{{scope.row.place}}</div>
          </template>
        </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <div class="line">{{ scope.row.status | statusToReadable }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="对该队伍的操作"
          width="200">
          <template slot-scope="scope">
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="通过测验" 
              placement="top">
              <el-button 
                icon="el-icon-success" circle
                type="success"
                class="button-lists-button"
                size="small"
                v-show="canPass(scope.row)"
                @click="projectPass(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="推荐至校级答辩" 
              placement="top">
              <el-button 
                icon="el-icon-arrow-up" circle
                type="success"
                class="button-lists-button"
                size="small"
                v-show="canRecommon(scope.row)"
                @click="projectRecommon(scope.row)"></el-button>
            </el-tooltip>
             <el-tooltip 
              class="item" 
              effect="dark" 
              content="不能通过测验" 
              placement="top">
              <el-button 
                icon="el-icon-error" circle
                type="danger"
                class="button-lists-button"
                size="small"
                v-show="canReject(scope.row)"
                @click="projectReject(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="取消校级答辩资格" 
              placement="top">
              <el-button 
                icon="el-icon-arrow-down" circle
                type="danger"
                class="button-lists-button"
                size="small"
                v-show="canRecRej(scope.row)"
                @click="projectRecRej(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="给出建议" 
              placement="top">
              <el-button 
                icon="el-icon-edit" circle 
                type="primary" 
                size="small" 
                class="button-lists-button"
                @click="Suggest(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip 
              class="item" 
              effect="dark" 
              content="队伍详情" 
              placement="top">
              <el-button 
                type="info" 
                class="button-lists-button"
                icon="el-icon-more" circle
                size="small"
                @click="openDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </pager>
    <el-dialog
      title="详情"
      :fullscreen="isPhone"
      v-dialogDrag
      :visible.sync="dialogVisible">
      <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates"/>
      <!-- 项目通过测验   -->
      <el-button round type="success"
        :disabled="!canPass(dialogRow)"
        @click="projectPass(dialogRow)">通过</el-button>
      <!-- 项目不通过测验   -->
      <el-button round type="danger"
        :disabled="!canReject(dialogRow)"
        @click="projectReject(dialogRow)">不通过</el-button>
      <!-- 推荐项目校级答辩   -->
      <el-button round type="success"
        :disabled="!canRecommon(dialogRow)"
        @click="projectRecommon(dialogRow)">推荐答辩</el-button>
      <!-- 取消项目校级答辩   -->
      <el-button round type="danger"
        :disabled="!canRecRej(dialogRow)"
        @click="projectRecRej(dialogRow)">取消答辩资格</el-button>
      <el-button round type="success" 
        size="small" 
        @click="Suggest(dialogRow)">给出建议</el-button>
    </el-dialog>
    <el-dialog
      title="给出建议"
      :visible.sync="suggestDialogVisible"
      :fullscreen="isPhone"
      v-dialogDrag>
      <el-input
        type="textarea"
        class="suggest-content"
        :rows="4"
        :cols="10"
        v-model="suggestion"
        placeholder="请填写您要给出的建议">
      </el-input>
      <el-button round type="success"
        @click="submitSuggestion()">提交</el-button>
      <el-button round type="info" @click="suggestDialogVisible = false">取消</el-button>
    </el-dialog>
  </div>
</template>

<script>
import Pager from '@/components/ArtPager'
import TeamInfoDisplay from '@/components/TeamInfoDisplay'
import statusMixin from '@/mixins/status'
import { mapGetters, mapMutations } from 'vuex';
import { showAsynConfirm } from '@/mixins/messageBox'
import { getTeammateInfo, getAdminTeams, teamIsPass, suggestion, toSchoolLevel, cancelToSchool} from '@/api/team'

export default {
  name: 'ManageCheck',
  mixins:[statusMixin],
  components: {
    Pager,
    TeamInfoDisplay
  },
  data() {
    return{
      suggestion: '',
      suggestDialogVisible: false,
      loading: false,
      status: 'all',
      projects: [],
      teammates:[],
      dialogVisible: false,
      dialogRow: {},
      BELONGTO_LIST: [
        '无挂靠单位',
        '校团委',
        '南洋书院',
        '崇实书院',
        '启德书院',
        '经金学院',
        '仲英书院',
        '电信学院',
        '宗濂书院',
        '文治书院',
        '能动学院',
        '彭康书院',
        '机械学院',
      ],
    }
  },
  computed: {
    ...mapGetters({
      user_info: 'user_info',
      isPhone: 'isPhone',
      currentTeamId: 'currentTeamId',
    })
  },
  mounted() {
    document.getElementById("manage-examine").scrollIntoView();
  },
  methods: {
    ...mapMutations ({
      saveCurrentTeamId: 'saveCurrentTeamId',
    }),
    //给pager方法
    getList: getAdminTeams,
    // 执行审核，通过或者不通过
    async _teamIsPass(team_id, data) {
      try {
        const return_data = await teamIsPass(team_id, data)
        this.changePages(1)
      } catch(message) {
        this.$message({
          type: 'error',
          message:  '操作失败，' + message
        })
      }
    },
    async _toSchoolLevel(team_id) {
      try {
        const return_data = await toSchoolLevel(team_id)
        this.changePages(1);
        this.$message({
          type: 'success',
          message:  "成功推荐到校级答辩"
        })
      } catch(message) {
        this.$message({
          type: 'error',
          message:  '操作失败，' + message
        })
      }
    },
    async _cancelToSchool(team_id) {
      try {
        const return_data = await cancelToSchool(team_id)
        this.changePages(1);
        this.$message({
          type: 'success',
          message:  "已取消校级答辩的资格"
        })
      } catch(message) {
        this.$message({
          type: 'error',
          message:  '操作失败，' + message
        })
      }
    },
    _check(row, msg, boolean) {   
      showAsynConfirm(msg, '已取消', (done) => {
        this._teamIsPass(row.team_id, boolean);
        done();
        this.changePages(1);
      });
    },
    _checkRec(row, msg, boolean) {   
      showAsynConfirm(msg, '已取消', (done) => {
        if(boolean)
        {
          this._toSchoolLevel(row.team_id);
        } 
        else if(!boolean) {
          this._cancelToSchool(row.team_id);
        }
        done();
        this.changePages(1);
      });
    },
    // 审核通过
    projectPass(row) {   
      this._check(row, '确认要通过该队伍的审核？', 1);
    },
    // 审核不通过
    projectReject(row) {    
      this._check(row, '确认不通过该队伍的审核？', 0);
    },
    // 推荐到校级
    projectRecommon(row) {   
      this._checkRec(row, '确认推荐该队伍到校级答辩？', 1);
    },
    // 取消推荐到校级
    projectRecRej(row) {   
      this._checkRec(row, '确认取消该队伍校级答辩的资格？', 0);
    },
    // 根据状态判断是否可以通过
    canPass(row) {
      return row.status == 5 || row.status == 6;
    },
    // 根据状态判断是否可以不通过
    canReject(row) {
      return row.status == 5 || row.status == 7;
    },
    canRecommon(row) {
      return row.status == 7 ;
    },
    canRecRej(row) {
      return row.status == 8 ;
    },
    async getTeams() {
      this.loading = true
      try {
        const data = await getAdminTeams(this.status)
        this.projects = data.data
        this.loading = false
      } catch(message) {
        this.$message({
          type: 'error',
          message: '获取队伍信息失败  ' + message
        });
      }
    },
    async getTeammateInfo() {
      this.loading = true
      try {
        const data = await getTeammateInfo(this.currentTeamId)
        this.teammates = data.data
        this.loading = false
      } catch(message) {
        this.$message({
          type: 'error',
          message: '获取该队伍信息失败  ' + message
        });
      }
    },
    // 切换页面
    changePages(i) {
      this.$refs.refresh.turn(i);
      this.dialogVisible = false;
    },
    // 切换面板时刷新
    handleClick() {
      setTimeout(() => {
        this.changePages(1)
      }, 200);
    },
    // teams详情窗口
    openDialog(row) {
      this.dialogRow = row;
      this.saveCurrentTeamId(row.team_id)
      setTimeout(() => {
        this.getTeammateInfo();
      }, 100);
      setTimeout(() => {
        this.dialogVisible = true;
      }, 300);
    },
    // 给出建议dialog
    Suggest(row) {
      this.suggestDialogVisible = true
      this.timelyRow = row
      this.saveCurrentTeamId(row.team_id)
    },
    submitSuggestion() {
      showAsynConfirm('确认给出该建议？', '已取消', (done) => {
        this._submitSuggestion(done)
      })
    },
    async _submitSuggestion(done) {
      done()
      try {
        const data = await suggestion(this.currentTeamId, this.suggestion)
        this.$message({
          type: 'success',
          message: '建议提交成功！' 
        });
        this.suggestDialogVisible = false
        this.suggestion = ''
        this.changePages(1)
      } catch(message) {
        this.$message({
          type: 'error',
          message: '提交未成功  ' + message
        });
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.manage-examine {
  .manage-examine-remain {
    margin: 0.5em 0;
    margin-left: -1.8em;
  }
  .button-lists-button {
    margin: 0 0.5em;
  }
  .title {
    font-size: 25px;
    font-family: "Microsoft YaHei";
    color: rgb(78, 64, 53);
    font-weight: bold;
    transform: scale(0.94, 1.08);
    margin-bottom: 15px;
    margin-left: -1.0em;
    margin-top: 0.3em;
  }
  .suggest-content {
    margin-bottom: 1em;
  }
  .title-line {
    display: flex;

    .title {
      flex-grow: 1;
    }
  }
}
.el-menu {
  background-color: #fdf5ee
}

@media screen and (max-width: 1000px) {
  .manage-dev{
    .title-line {
      font-size: 16px;
      .title {
        width: 80px;
        font-size: 16px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 16px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 15px;
    }
    .el-table .cell {
      font-size: 15px;
    }
    .el-table th, .el-table td {
      padding: 8px 0;
    }
  }
}

@media screen and (max-width: 700px) {
  .manage-dev{
    .title-line {
      font-size: 14px;
      .title {
        width: 80px;
        font-size: 14px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 14px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 14px;
    }
    .el-table .cell {
      font-size: 14px;
    }
    .el-table th, .el-table td {
      padding: 6px 0;
    }
  }
}


@media screen and (max-width: 410px) {
  .manage-dev{
    .title-line {
      font-size: 12px;
      .title {
        width: 70px;
        font-size: 12px;
        margin-left: 6px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 12px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 12px;
    }
    .el-table .cell {
      font-size: 12px;
    }
    .el-table th, .el-table td {
      padding: 4px 0;
    }
  }
}
</style>

