<template>
  <div class="manage-check manage-dev">
    <div class="title-line">
      <p class="title">项目审核</p>
      <p class="org">当前挂靠单位：{{ BELONGTO_LIST[this.user_info.org] }}</p>
    </div>
    <el-tabs class="table-tab" v-model="status" @tab-click="handleClick">
      <el-tab-pane label="全部项目" name="all"></el-tab-pane>
      <el-tab-pane label="未提交项目" name="0"></el-tab-pane>
      <el-tab-pane label="未审核项目" name="1"></el-tab-pane>
      <el-tab-pane label="已通过项目" name="3"></el-tab-pane>
      <el-tab-pane label="未通过项目" name="2"></el-tab-pane>
    </el-tabs>
    <!-- <p class="manage-check-remain title">剩余项目数：{{ projectsNum }}</p> -->
    <pager
      :pageSize="6"
      :requestFunc="getList"
      :errorMsg="'无法获取'"
      :extraData="status"
      ref="refresh"
    >
      <el-table v-loading="loading" class="table-project" slot-scope="props" :data="props.list">
        <el-table-column label="队伍名称">
          <template slot-scope="scope">
            <div class="line">{{scope.row.team_name}}</div>
          </template>
        </el-table-column>
        <el-table-column label="项目名称">
          <template slot-scope="scope">
            <div class="line">{{scope.row.project_name}}</div>
          </template>
        </el-table-column>
        <el-table-column label="实践地点" width="130">
          <template slot-scope="scope">
            <div class="line">{{scope.row.place}}</div>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="220">
          <template slot-scope="scope">
            <div class="line">{{ scope.row.status | statusToReadable }}</div>
          </template>
        </el-table-column>
        <el-table-column label="对该队伍的操作" width="240">
          <template slot-scope="scope">
            <div class="button-lists">
              <el-tooltip class="item" effect="dark" content="通过测验" placement="top">
                <el-button
                  icon="el-icon-success"
                  circle
                  type="success"
                  class="button-lists-button"
                  size="small"
                  v-show="canPass(scope.row)"
                  @click="projectPass(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="不能通过测验" placement="top">
                <el-button
                  icon="el-icon-error"
                  circle
                  type="danger"
                  class="button-lists-button"
                  size="small"
                  v-show="canReject(scope.row)"
                  @click="projectReject(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="给出建议" placement="top">
                <el-button
                  icon="el-icon-edit"
                  circle
                  type="primary"
                  size="small"
                  class="button-lists-button"
                  @click="Suggest(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="队伍详情" placement="top">
                <el-button
                  type="info"
                  class="button-lists-button"
                  icon="el-icon-more"
                  circle
                  size="small"
                  @click="openDialog(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="发回重改" placement="top">
                <el-button
                  icon="el-icon-back"
                  circle
                  type="danger"
                  size="small"
                  class="button-lists-button"
                  v-show="canRemake(scope.row)"
                  @click="projectRemake(scope.row)"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </pager>
    <el-dialog title="详情" :visible.sync="dialogVisible" :fullscreen="isPhone" v-dialogDrag>
      <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates"/>
      <el-button
        round
        type="success"
        :disabled="!canPass(dialogRow)"
        @click="projectPass(dialogRow)"
      >通过</el-button>
      <el-button
        round
        type="danger"
        :disabled="!canReject(dialogRow)"
        @click="projectReject(dialogRow)"
      >不通过</el-button>
      <el-button
        round
        type="danger"
        :disabled="!canRemake(dialogRow)"
        @click="projectRemake(dialogRow)"
      >发回重改</el-button>
      <el-button round type="success" size="small" @click="Suggest(dialogRow)">给出建议</el-button>
    </el-dialog>
    <el-dialog title="给出建议" :visible.sync="suggestDialogVisible" :fullscreen="isPhone" v-dialogDrag>
      <el-input
        type="textarea"
        class="suggest-content"
        :rows="4"
        :cols="10"
        v-model="suggestion"
        placeholder="请填写您要给出的建议"
      ></el-input>
      <el-button round type="success" @click="submitSuggestion()">提交</el-button>
      <el-button round type="info" @click="suggestDialogVisible = false">取消</el-button>
    </el-dialog>
    <!-- 需要做一个弹出框来显示具体消息，或者更高大上的窒息操作 -->
  </div>
</template>

<script>
import Pager from "@/components/ArtPager";
import TeamInfoDisplay from "@/components/TeamInfoDisplay";
import statusMixin from "@/mixins/status";
import { mapGetters, mapMutations } from "vuex";
import {
  getTeammateInfo,
  getAdminTeams,
  auditTeam,
  withDraw,
  suggestion
} from "@/api/team";
import { showAsynConfirm } from "@/mixins/messageBox";
import { setTimeout } from "timers";

export default {
  name: "ManageCheck",
  mixins: [statusMixin],
  components: {
    Pager,
    TeamInfoDisplay
  },
  data() {
    return {
      suggestion: "",
      suggestDialogVisible: false,
      timelyRow: "",
      dialogVisible: false,
      dialogRow: {},
      teammates: [],
      loading: false,
      status: "all",
      projects: [],
      BELONGTO_LIST: [
        "无挂靠单位",
        "校团委",
        "南洋书院",
        "崇实书院",
        "启德书院",
        "经金学院",
        "仲英书院",
        "电信学院",
        "宗濂书院",
        "文治书院",
        "能动学院",
        "彭康书院",
        "机械学院"
      ]
    };
  },
  computed: {
    ...mapGetters({
      user_info: "user_info",
      isPhone: "isPhone",
      currentTeamId: "currentTeamId"
    }),
    projectsNum() {
      return this.$refs.refresh.total;
    }
  },
  methods: {
    ...mapMutations({
      saveCurrentTeamId: "saveCurrentTeamId"
    }),
    //给pager方法
    getList: getAdminTeams,
    async getTeams() {
      this.loading = true;
      try {
        const data = await getAdminTeams(this.status);
        this.projects = data.data;
        this.loading = false;
      } catch (message) {
        this.$message({
          type: "error",
          message: "获取队伍信息失败  " + message
        });
      }
    },
    async getTeammateInfo() {
      this.loading = true;
      try {
        const data = await getTeammateInfo(this.currentTeamId);
        this.teammates = data.data;
        this.loading = false;
      } catch (message) {
        this.$message({
          type: "error",
          message: "获取该队伍信息失败  " + message
        });
      }
    },
    // 切换面板时刷新
    handleClick() {
      setTimeout(() => {
        this.changePages(1);
      }, 200);
    },
    // teams详情窗口
    openDialog(row) {
      this.dialogRow = row;
      this.saveCurrentTeamId(row.team_id);
      setTimeout(() => {
        this.getTeammateInfo();
      }, 100);
      setTimeout(() => {
        this.dialogVisible = true;
      }, 300);
    },
    _check(row, msg, boolean) {
      showAsynConfirm(msg, "已取消", done => {
        this._auditTeam(row.team_id, boolean);
        done();
        this.changePages(1);
      });
    },
    // 审核通过
    projectPass(row) {
      this._check(row, "确认该队伍的审核结果为通过？", 1);
    },
    // 审核不通过
    projectReject(row) {
      this._check(row, "确认该队伍的审核结果为不通过？", 0);
    },
    // 发回重改
    projectRemake(row) {
      showAsynConfirm(
        "确认将该队伍发回重改？队伍状态将转为未提交审核状态",
        "已取消",
        done => {
          this._withDraw(row, done);
        }
      );
    },
    // 执行审核，通过或者不通过
    async _auditTeam(team_id, data) {
      try {
        const return_data = await auditTeam(team_id, data);
        this.changePages(1);
        this.$message({
          type: "success",
          message: "审核完成！"
        });
      } catch (message) {
        this.$message({
          type: "error",
          message: "审核失败，" + message
        });
      }
    },
    async _withDraw(row, done) {
      done();
      try {
        const data = await withDraw(row.team_id);
        this.changePages(1);
        this.$message({
          type: "success",
          message: "已发回重改！"
        });
      } catch (message) {
        this.$message({
          type: "error",
          message: "撤回失败，" + message
        });
      }
      this.changePages(1);
    },
    canPass(row) {
      return row.status == 1 || row.status == 2;
    },
    // 根据状态判断是否可以不通过
    canReject(row) {
      return row.status == 1 || row.status == 3;
    },
    // 根据状态判断是否可以发回重改
    canRemake(row) {
      return row.status == 2;
    },
    //给出建议dialog
    Suggest(row) {
      this.suggestDialogVisible = true;
      this.timelyRow = row;
      this.saveCurrentTeamId(row.team_id);
    },
    submitSuggestion() {
      showAsynConfirm("确认给出该建议？", "已取消", done => {
        this._submitSuggestion(done);
      });
    },
    async _submitSuggestion(done) {
      done();
      try {
        const data = await suggestion(this.currentTeamId, this.suggestion);
        this.$message({
          type: "success",
          message: "成功给出建议！"
        });
        this.suggestDialogVisible = false;
        this.suggestion = "";
        this.changePages(1);
      } catch (message) {
        this.$message({
          type: "error",
          message: "未成功给出建议  " + message
        });
      }
    },
    changePages(i) {
      this.$refs.refresh.turn(i);
    }
  }
};
</script>

<style lang="scss" scoped>
.manage-check {
  .manage-check-remain {
    margin: 1em 0;
    margin-left: -1.8em;
  }

  .table-project {
    margin: 0 0 1em 0;
  }

  .title {
    font-size: 25px;
    font-family: "Microsoft YaHei";
    color: rgb(78, 64, 53);
    font-weight: bold;
    transform: scale(0.94, 1.08);
    margin-bottom: 15px;
    margin-left: -1.0em;
    margin-top: 0.3em;
  }

  .warning {
    margin: 1em 0;
    color: red;
    font-weight: bold;
    height: 80px;
  }

  .button-lists {
    display: inline-block;
  }

  .button-lists-button {
    margin: 0.5em;
  }

  .suggest-content {
    margin-bottom: 1em;
  }

  .title-line {
    display: flex;
    .title {
      flex-grow: 1;
    }
  }
}
</style>

<style lang="scss">
@media screen and (max-width: 1000px) {
  .manage-dev{
    .title-line {
      font-size: 16px;
      .title {
        width: 80px;
        font-size: 16px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 16px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 15px;
    }
    .el-table .cell {
      font-size: 15px;
    }
    .el-table th, .el-table td {
      padding: 8px 0;
    }
  }
}

@media screen and (max-width: 700px) {
  .manage-dev{
    .title-line {
      font-size: 14px;
      .title {
        width: 80px;
        font-size: 14px;
        margin-left: 10px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 14px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 14px;
    }
    .el-table .cell {
      font-size: 14px;
    }
    .el-table th, .el-table td {
      padding: 6px 0;
    }
  }
}


@media screen and (max-width: 410px) {
  .manage-dev{
    .title-line {
      font-size: 12px;
      .title {
        width: 70px;
        font-size: 12px;
        margin-left: 6px;
      }
    }
    .table-tab {
      .el-tabs__nav {
        .el-tabs__item {
          font-size: 12px;
        }
      }
    }
    .el-table th > .cell {
      font-size: 12px;
    }
    .el-table .cell {
      font-size: 12px;
    }
    .el-table th, .el-table td {
      padding: 4px 0;
    }
  }
}
</style>

